export default {
  apiName: 'Vacancy Assistant',
  endpoints: [
    {
      title: 'auth',
      items: [
        {
          method: 'post',
          path: '/register',
          label: 'Register',
          req: `{
          "name": "String",
          "email": "String",
          "password": "String"
        }`,
        },
        {
          method: 'post',
          path: '/login',
          label: 'Login',
          req: `{
            "email": "String",
            "password": "String"
          }`,
        },
        {
          method: 'get',
          path: '/current',
          label: 'Current',
          req: `headers: {
            Authorization: 'Bearer access',
            'x-refresh-token': refresh,
          }`,
        },
        {
          method: 'get',
          path: '/refresh',
          label: 'Refresh',
          req: `headers: {
            Authorization: 'Bearer access',
            'x-refresh-token': refresh,
          }`,
        },
        {
          method: 'post',
          path: '/setRecoveryPassword',
          label: 'setRecoveryPassword',
          req: `{
            "email": "String"
          }`,
        },
        {
          method: 'patch',
          path: '/setNewPassword',
          label: 'setNewPassword',
          req: `{
            "email": "String",
            "recoveryPassword": "String",
            "password": "String"
          }`,
        },
      ],
    },
    {
      title: 'vacancy',
      items: [
        {
          method: 'post',
          path: '',
          label: 'Set new vacancy',
          req: `{
            "companyName": "String",
            "URL": "String",
            "location": "String",
            "stack": "String",
            "phone": "String",
            "favorite": "Boolean",
            "status": "String"
          }`,
        },
        {
          method: 'patch',
          path: '/:vacancyId',
          label: 'Update vacancy',
          req: `{
            "any in set req": "String"
          }`,
        },
        {
          method: 'delete',
          path: '/:vacancyId',
          label: 'Delete vacancy',
          req: ``,
        },
      ],
    },
  ],
};
